.wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(40, 44, 52, 0.8);
  z-index: 1000;
}

.modal {
  width: 380px;
  height: auto;
  border-radius: 10px;
  padding: 20px;
  background: #ffffff;
  border: 2px solid #b8b7b7;
  min-width: 320px;
}

.closeBtnWrapper {
  text-align: right;
  margin-bottom: 10px;
}

.btnClose {
  border: none;
  background: none;
  width: auto;
  margin: 0;
  padding: 0;
  outline: none;
  cursor: pointer;
}

.closeIcon {
  font-size: 32px;
}

.formWrapper {
  padding-top: 15px;
  display: flex;
  flex-direction: column;
  overflow: auto;

}

.uploadBtn {
  border: none;
  background: linear-gradient(#36b6c2, #c391e5);
  font-size: 14px;
  width: 140px;
  height: 25px;
  margin: 20px auto;
  cursor: pointer;
}

.previewIcon {
  width: 70px;
  height: auto;
  border-radius: 50%;
}

.active {
  opacity: 1;
  visibility: visible;

}

.notActive {
  opacity: 0;
  visibility: hidden;
}


